/*Кнопка "В корзину"*/
.in-basket {
  position: relative;
  top: 60px;
  left: 87px;
  width: 246px;
  height: 47px;
  font-family: Roboto, sans-serif;
  font-weight: 300;
  font-size: 18px;
  text-align: center;
  color: #ffffff;
  background: -webkit-gradient(linear, left top, right top, from(#ff65a5), color-stop(100.22%, #ff8076));
  background: -webkit-linear-gradient(left, #ff65a5 0%, #ff8076 100.22%);
  background: -o-linear-gradient(left, #ff65a5 0%, #ff8076 100.22%);
  background: linear-gradient(90deg, #ff65a5 0%, #ff8076 100.22%);
  -webkit-box-shadow: 0 2px 8px rgba(255, 101, 165, 0.6), inset 0 1px 1px #ff8076;
          box-shadow: 0 2px 8px rgba(255, 101, 165, 0.6), inset 0 1px 1px #ff8076;
  border-radius: 8px;
  border: none;
}
.in-basket:hover {
  cursor: pointer;
}
.in-basket:disabled,
.in-basket[disabled] {
  background: -webkit-gradient(linear, left top, right top, from(#848283), color-stop(100.22%, #bebebe));
  background: -webkit-linear-gradient(left, #848283 0%, #bebebe 100.22%);
  background: -o-linear-gradient(left, #848283 0%, #bebebe 100.22%);
  background: linear-gradient(90deg, #848283 0%, #bebebe 100.22%);
  -webkit-box-shadow: 0 2px 8px rgba(79, 79, 79, 0.6);
          box-shadow: 0 2px 8px rgba(79, 79, 79, 0.6);
  cursor: default;
}
.in-basket-click {
  overflow: hidden;
  position: relative;
}
.in-basket-click span {
  z-index: 20;
}
.in-basket-click:after {
  position: absolute;
  top: -50px;
  left: -65px;
  width: 5px;
  height: 155px;
  content: "";
  background: #ffffff;
  filter: blur(7px);
  -webkit-filter: blur(7px);
  border-right: 10px solid #ffffff;
  -webkit-transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  -o-transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  -webkit-transform: rotate(-35deg);
  -ms-transform: rotate(-35deg);
  transform: rotate(-35deg);
}
.in-basket-click:active:after {
  left: 120%;
  -webkit-transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  -o-transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
}
