  position: relative;
  display: inline-block;
  top: -2px;
  left: 22px;
  width: 19px;
  height: 19px;
  vertical-align: middle;
  border: 1px solid #333333;
}
.checkbox:checked + .checkbox-discount::before {
  position: absolute;
  top: -1px;
  left: -1px;
  content: url(../../img/checkbox-checked-19.png);
  display: block;
  width: 16px;
  height: 16px;
}
.text-discount {
  display: inline-block;
  margin-left: 25px;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 400;
}
/*Сброс*/
.drop-down {
  display: block;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  margin: 0 auto;
  padding-bottom: 5px;
  font-size: 18px;
  text-align: center;
}
.drop-down.hide {
  display: none;
}
.drop-down-icon {
  position: relative;
  top: -1px;
  right: 1px;
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: top;
  background: url(../../img/product-catalogue-sprite-min.png) no-repeat;
  background-position: -60px -19px;
}
.drop-down a {
  position: relative;
  top: 10px;
  right: 2px;
  display: inline-block;
  text-decoration: underline;
  vertical-align: top;
}
/*Кастомизация бегунков*/
::-webkit-scrollbar-button {
  background-repeat: no-repeat;
  width: 7px;
  height: 7px;
}
::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0.5);
}
::-webkit-scrollbar-thumb {
  border-radius: 15px;
  background-color: #a1b3bf;
}
::-webkit-scrollbar-thumb:hover {
  background-color: rgba(161, 179, 191, 0.5);
}
::-webkit-resizer {
  background-repeat: no-repeat;
  width: 7px;
}
::-webkit-scrollbar {
  width: 7px;
}


